@use "../../global/variable.scss" as *;
.m-author-meta {
  &__wrapper {
    display: flex;
    align-items: center;
    padding: $gutter-s 0;
  }

  &__image {
    height: 30px;
    width: 30px;
    margin-right: $gutter-xs;
    border-radius: 30px;
    overflow: hidden;
  }

  &__title {
    font-size: $font-xs;
  }
}